import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import Seo from "../components/Seo"
import { EventCard } from "../components/EventCard"
import EventArchivePagination from "../components/EventArchivePagination"

import { Stack, StackDivider } from "@chakra-ui/react"

const EventArchive = props => {
  const {
    data: {
      allWpTribeEvent: { nodes, pageInfo },
      customEvent,
    },
    pageContext: { archiveType, archivePath },
  } = props
  const posts = nodes

  if (!posts.length) {
    return (
      <Layout isHomePage>
        <Seo title="Automotive News & Events" />
        <p>
          No blog posts found. Add posts to your WordPress site and they'll
          appear here!
        </p>
      </Layout>
    )
  }

  return (
    <Layout
      isHomePage
      {...pageInfo}
      archivePath={archivePath}
      archiveType={archiveType}
    >
      <Seo title="Automotive News & Events" />
      <Stack
        as="ol"
        divider={<StackDivider borderColor="gray.200" />}
        spacing={4}
        mb={4}
        p={8}
        listStyleType="none"
      >
        {posts.map((post, index) => {
          const title = post.title
          const featuredImage = {
            fluid:
              post.featuredImage?.node?.localFile?.childImageSharp
                ?.gatsbyImageData,
            alt: post.featuredImage?.node?.alt || ``,
          }

          const venue = customEvent.nodes.filter(node => {
            return node.slug === post.slug
          })

          return (
            <>
              <EventCard
                key={index}
                title={title}
                featuredImage={featuredImage}
                uri={post.uri}
                date={post.date}
                excerpt={post.excerpt}
                author={post.author.node}
                categories={post.tribeEventsCats.nodes}
                venue={venue[0].venue}
                startDate={post.startDate}
                endDate={post.endDate}
              />
            </>
          )
        })}
      </Stack>
      {/* <Pagination>
            {previousPagePath && (
              <>
                <AppButton url={previousPagePath} text="< Previous"></AppButton>
                <br />
              </>
            )}
            {nextPagePath && (
              <AppButton url={nextPagePath} text="Next >"></AppButton>
            )}
          </Pagination> */}
      {/* <EventArchivePagination
        {...pageInfo}
        archiveType={archiveType}
        archivePath={archivePath}
      /> */}
    </Layout>
  )
}

export default EventArchive

export const pageQuery = graphql`
  query WordPressTribeEventArchive(
    $offset: Int!
    $perPage: Int!
    $categoryDatabaseId: Int
  ) {
    allWpTribeEvent(
      sort: { fields: startDate, order: ASC }
      limit: $perPage
      skip: $offset
      filter: {
        tribeEventsCats: {
          nodes: { elemMatch: { databaseId: { eq: $categoryDatabaseId } } }
        }
      }
    ) {
      nodes {
        excerpt
        uri
        slug
        date(formatString: "MMMM DD, YYYY")
        startDate
        endDate
        title
        excerpt
        featuredImage {
          node {
            altText
            localFile {
              childImageSharp {
                gatsbyImageData(placeholder: TRACED_SVG, layout: FULL_WIDTH)
              }
            }
          }
        }
        author {
          node {
            name
            uri
          }
        }
        tribeEventsCats {
          nodes {
            name
            uri
          }
        }
      }
      pageInfo {
        hasNextPage
        hasPreviousPage
        currentPage
        pageCount
      }
    }
    customEvent: allTribeEvent(sort: { fields: start_date, order: ASC }) {
      nodes {
        slug
        title
        venue {
          venue
          address
          city
        }
      }
    }
  }
`
