import React from "react"
import { Link as GatsbyLink } from "gatsby"
import parse from "html-react-parser"
import { AppButton } from "./atoms/button"
import { Stack, Heading, Text, Link } from "@chakra-ui/react"

import { PostLiContent, PostListMeta } from "../styles/contentStyles"
import { EventMeta } from "../styles/postStyles"

import { ChakraImage } from "./atoms/chakraImage"

export const EventCard = props => {
  let catTotal = props.categories.length
  return (
    <>
      <li key={props.uri}>
        <Stack
          direction={["column", "column", "row", "row"]}
          className="post-list-item"
          itemScope
          itemType="http://schema.org/Article"
        >
          <ChakraImage
            image={props.featuredImage.fluid}
            alt={props.featuredImage.alt}
            minWidth={["100%", "50%"]}
            objectFit="cover"
            maxHeight="16rem"
          />
          <PostLiContent>
            <header>
              <Heading as="h3" size="md" color="brand.600">
                <Link
                  as={GatsbyLink}
                  to={props.uri}
                  itemProp="url"
                  color="brand.600"
                >
                  <span itemProp="headline">{parse(props.title)}</span>
                </Link>
              </Heading>
              <EventMeta startDate={props.startDate} endDate={props.endDate} />
            </header>
            <Text
              as="section"
              fontSize="sm"
              isTruncated
              noOfLines={4}
              itemProp="description"
            >
              {parse(props.excerpt)}
            </Text>
            <Text fontWeight="600">{props.venue.venue}</Text>
            <Text>
              {props.venue.address}, {props.venue.city}
            </Text>
            <AppButton url={props.uri} text="Learn More" />
          </PostLiContent>
        </Stack>
      </li>
    </>
  )
}
